	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Это эффективное упражнение, направленное на проработку трицепса. Также небольшая нагрузка поступает на верхнюю дельту и большую грудную мышцу. Такие отжимания рекомендуются для тех, кто еще не научился отжиматься от брусьев.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>В качестве инвентаря для данного упражнения необходимо использовать одну или две скамьи.</li>\n  <li>Исходное положение: сядьте на край скамьи и поставьте руки шире ширины плеч. Упритесь ладонями в край скамьи, расположив ноги пятками на второй лавке.</li>\n  <li>На вдохе – медленно опускайте таз вниз, сгибая руки в локтях до прямого угла. Держите спину ровно, не выпячивая таз вперед.</li>\n  <li>На выдохе – используя трицепс, плавно выполните подъем тела вверх к исходному положению. В верхней точке упражнения сделайте небольшую паузу.</li>\n  <li>Сделайте требуемое количество повторений.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Это упражнение можно выполнять, постепенно увеличивая сложность. Самый простой вариант этого упражнения – поставить ноги на пол, сгибая их в коленях. После этого возможно использование лавки и своего веса. Наиболее эффективным упражнение будет, когда Вы кладете блины от штанги себе на бедра.</li>\n  <li>Выполняя упражнение, старайтесь держать спину ровно и максимально близко к краю скамьи.</li>\n  <li>Отжимайтесь так, чтобы плечи находились ниже уровня локтей, а при подъеме были полностью прямые.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/9y7IITH_lrQ\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/obratnie-otjimania","efficiency":"5","title":"Обратные отжимания","image":"/static/4-04-0.png","image_2":"/static/4-04-1.png","image_3":"","image_4":"","filterby":"triceps","date":"April 12, 2017","injury":"4","bodypart":"Трицепс","filtertype":"base","type":"Базовое","filterinventory":"ownweight","inventory":"Собственный вес","filterplace":null}}},"pathContext":{}}

